import { Component, OnInit, Input, Inject, AfterViewInit, ChangeDetectorRef, Output, EventEmitter, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { RequisitionDetailMasterService, RequisitionDetail,ErrorHandleService, ItemMasterService, CurrentUserService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxDateTimeInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxdatetimeinput';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-requisition-detail-master-edit',
  templateUrl: './requisition-detail-master-edit.component.html',
  styleUrls: ['./requisition-detail-master-edit.component.scss']
})
export class RequisitionDetailMasterEditComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('reqDetailEditGrid') reqDetailEditGrid: jqxGridComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('fComboBox') fComboBox: jqxComboBoxComponent;
  @ViewChild('tComboBox') tComboBox: jqxComboBoxComponent;
  @ViewChild('datetime') datetime: jqxDateTimeInputComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;

  @Input('RequisitionDetailEditData') RequisitionDetailEditData;
  @Output() UpdatedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

  EditData: RequisitionDetail;
  requisitionDetailForm: FormGroup;
  source: any;
  dataAdapter: any;
  columns: any[];
  apiUrl: string;
  ready: boolean = false;
  requisition_item: Array<any> = [];
  contractorsource: any;
  contractorAdapter: any;
  store_data: any;
  storeAdapter: any;

  userData: any = {};

  constructor(
    private fb: FormBuilder,
    @Inject('API_URL') apiUrl: string,
    private cdr: ChangeDetectorRef,
    private rdms: RequisitionDetailMasterService,
    private ehs: ErrorHandleService,
    private itemService: ItemMasterService,
    private cus: CurrentUserService
  ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.createForm();
  }

  ngOnInit() {
    this.getItem();
    this.EditData = new RequisitionDetail(this.RequisitionDetailEditData);
    this.requisitionDetailForm.setValue(this.EditData);
    this.store_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'store?limit=all',
        totalrecords: 0
      };
    this.storeAdapter = new jqx.dataAdapter(this.store_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }, loadComplete: () => {
        this.fComboBox.selectItem(this.RequisitionDetailEditData.from_store_id);
        this.tComboBox.selectItem(this.RequisitionDetailEditData.to_store_id);
      }
    });

  }

  ngAfterViewInit() {
    this.jqxLoader.open();
    this.rdms.show(this.RequisitionDetailEditData.id).subscribe(
      result => {

        this.requisition_item = result['data'].requisition_item_details;

        for (var _i = 0; _i < this.requisition_item.length; _i++) {
          this.requisition_item[_i]['name'] = this.requisition_item[_i]['item'] && this.requisition_item[_i]['item'].name;
          this.requisition_item[_i]['unit_name'] = this.requisition_item[_i]['item'] && this.requisition_item[_i]['item']['unit'] && this.requisition_item[_i]['item']['unit'].name;
        }
        this.source.totalrecords = this.requisition_item.length - 1;
        this.source.localdata = this.requisition_item;
        this.reqDetailEditGrid.updatebounddata();
        this.jqxLoader.close();
      },
      error => {
        console.log(error);
      }
    );

    this.cdr.detectChanges();
  }

  createForm() {
    this.requisitionDetailForm = this.fb.group({
      'indent_no': [null, Validators.required],
      'indent_date': [null, Validators.required],
    });
  }

  getItem() {
    this.source =
      {
        localdata: {},
        datatype: 'json',
        datafields:

          [
            { name: 'name', type: 'string' },
            { name: 'item_id', type: 'string' },
            { name: 'quantity', type: 'number' },
            { name: 'unit_id', type: 'number' },
            { name: 'unit_name', type: 'string' },
            { name: 'remarks', type: 'string' },
          ],
        id: 'item_id'
      };

    this.dataAdapter = new jqx.dataAdapter(this.source);

    this.contractorsource =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [

          { name: 'item_id', type: 'string' },
          { name: 'name', type: 'string' },
        ],
        data: this.userData,
        url: this.apiUrl + 'item?limit=all'
      };

    this.contractorAdapter = new jqx.dataAdapter(this.contractorsource, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        for (var _i = 0; _i < tData.length; _i++) {
          tData[_i]['item_id'] = tData[_i]['id'];
        }
        return tData;
      }
    });

    this.columns =
      [
        {
          text: 'Item Name', datafield: 'item_id', displayfield: 'name', columntype: 'combobox',
          initeditor: (row: number, cellvalue: string, editor: any, celltext: string, cellwidth: number, cellheight: number): void => {
            editor.jqxComboBox({
              source: this.contractorAdapter,
              displayMember: "name",
              valueMember: "item_id",
            });
          },
          cellvaluechanging: (row: number, datafield: string, columntype: any, oldvalue: any, newvalue: any): void => {
            if (newvalue != oldvalue) {
              let currentRow = this.reqDetailEditGrid.getrowdata(row).boundindex;
              this.itemService.show(newvalue['value']).subscribe(
                result => {
                  if(result['data']['quantity']){
                    this.reqDetailEditGrid.setcellvalue(currentRow,'quantity',result['data'].quantity);
                  }else{
                    this.reqDetailEditGrid.setcellvalue(currentRow,'quantity',0);
                  }
                  if(result['data']['unit']){
                    this.reqDetailEditGrid.setcellvalue(currentRow,'unit_name',result['data']['unit'].name);
                    this.reqDetailEditGrid.setcellvalue(currentRow,'unit_id',result['data'].unit_id);
                  }

                },
                error => {
                  console.log(error);
                }
              )
            };
          },
          // cellsrenderer: (index: number, datafield: string, value: any, defaultvalue: any, column: any, rowdata: any): void => {
          //   console.log(index);
          //   if(rowdata.item_id){
              
          //     this.itemService.show(rowdata.item_id).subscribe(
          //       result => {
          //         if(result['data']['unit']){
          //           this.reqDetailEditGrid.setcellvaluebyid(String(index+1),'unit_name',result['data']['unit'].name);
          //           this.reqDetailEditGrid.setcellvaluebyid(String(index+1),'unit_id',result['data'].unit_id);
          //         }
          //         if(result['data']['quantity']){
          //           this.reqDetailEditGrid.setcellvaluebyid(String(index+1),'quantity',result['data'].quantity);
          //         }else{
          //           this.reqDetailEditGrid.setcellvaluebyid(String(index+1),'quantity',0);
          //         }
          //       },
          //       error => {
          //         console.log(error);
          //       }
          //     )
          //   }
          // }
        },
        { text: 'Quantity', columntype: 'textbox', datafield: 'quantity' },
        { text: 'Unit', datafield: 'unit_id', displayfield:'unit_name', columntype: 'textbox' },
        { text: 'Remarks', datafield: 'remarks', columntype: 'textbox' },
        {
          text: 'Delete', datafield: 'Delete', columntype: 'button', width: 70,
          cellsrenderer: (): string => {
            return 'Delete';
          },
          buttonclick: (row: number): void => {

            let rowID = this.reqDetailEditGrid.getrowid(row);
            this.reqDetailEditGrid.deleterow(rowID);
          }
        }
      ];

  }

  // rendertoolbar = (toolbar: any): void => {
  //   console.log('Req Edit Toolbar Rendered')
  //   let container = document.createElement('div');
  //   container.style.margin = '5px';
  //   let ReqDetailMasterEditTitle = document.createElement('div');
  //   let ReqDetailMasterEditAddItem = document.createElement('div');

  //   ReqDetailMasterEditTitle.id = 'ReqDetailMasterEditTitle';
  //   ReqDetailMasterEditAddItem.id = 'ReqDetailMasterEditAddItem';

  //   ReqDetailMasterEditTitle.style.cssText = 'float: left;padding:5px;margin-right:10px;font-weight:bold;';
  //   ReqDetailMasterEditAddItem.style.cssText = 'float: right';

  //   ReqDetailMasterEditTitle.innerHTML = 'Requisition Details:';

  //   container.appendChild(ReqDetailMasterEditTitle);
  //   container.appendChild(ReqDetailMasterEditAddItem);
  //   toolbar[0].appendChild(container);

  //   let addRowButton = jqwidgets.createInstance('#ReqDetailMasterEditAddItem', 'jqxButton', { width: 155, value: 'Add New Item' });


  //   addRowButton.addEventHandler('click', () => {
  //     let datarow = this.generaterow();
  //     this.reqDetailEditGrid.addrow(null, datarow);
  //   })

  // };

  addNewItem(){
    let datarow = this.generaterow();
    this.reqDetailEditGrid.addrow(null, datarow);
  }

  generaterow(): any {
    let row = {};
    row['item_id'] = '';
    row['quantity'] = ''
    row['unit_id'] = '';
    row['remarks'] = '';
    return row;
  }
  saveBtn(post) {
    post['requisitionItemDetails'] = this.reqDetailEditGrid.getrows();
    post['from_store_id'] = this.fComboBox.val();
    post['to_store_id'] = this.tComboBox.val();
    this.myValidator.validate(document.getElementById('requisitionDetailForm'));
    if (post['from_store_id'] && post['to_store_id']) {
      this.jqxLoader.open();
      this.rdms.update(this.RequisitionDetailEditData.id, post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.UpdatedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        });
    }
  }

  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
    [
      { input: '.indent_noInput', message: 'Indent no is required!', action: 'keydown, blur', rule: 'required' },
      {
        input: '.indent_dateInput', message: ' Indent date is required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
          let data = this.datetime.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.from_store_id', message: 'From store is required!', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.fComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.to_store_id', message: 'To store is required!', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.tComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      }

    ];

}



