import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RequisitionDetailMasterComponent } from './requisition-detail-master.component';
import { CommonModule } from '@angular/common';

const routes: Routes = [
  {
      path: '', 
      component: RequisitionDetailMasterComponent,
     
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RequisitionDetailMasterRoutingModule { }


