import { Component, OnInit, ViewChild, Inject, ComponentFactoryResolver, ElementRef, ViewContainerRef } from '@angular/core';
import { routerTransition } from '../../../router.animations';
import { jqxWindowComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxwindow';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { RequisitionDetailMasterService, StoreService, QuickaccessComponent, CurrentUserService } from '../../../shared';
import { RequisitionDetailMasterAddComponent } from './requisition-detail-master-add/requisition-detail-master-add.component';
import { RequisitionDetailMasterEditComponent } from './requisition-detail-master-edit/requisition-detail-master-edit.component';
import { RequisitionItemDetailsViewComponent } from './requisition-item-details-view/requisition-item-details-view.component';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';

@Component({
  selector: 'app-requisition-detail-master',
  templateUrl: './requisition-detail-master.component.html',
  styleUrls: ['./requisition-detail-master.component.scss']
})
export class RequisitionDetailMasterComponent implements OnInit {
  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('myGrid') myGrid: jqxGridComponent;
  @ViewChild('myWindow') myWindow: jqxWindowComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('Insert', { read: ViewContainerRef }) Insert: ViewContainerRef;
  @ViewChild('quickAccess', { read: ViewContainerRef }) quickAccess: ViewContainerRef;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;

  editrow: number = -1;
  apiUrl: string;
  source: any;
  dataAdapter: any;
  columns: any[];
  report: boolean = false;
  request_url: any;
  store_item: any = [];

  userData: any = {};

  constructor(
    private rdm: RequisitionDetailMasterService,
    private componentFactoryResolver: ComponentFactoryResolver,
    private ss: StoreService,
    @Inject('API_URL') apiUrl: string,
    private cus: CurrentUserService,
  ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.getList();
  }

  ngOnInit() {
    this.createQuickAccess();
  }

  ngAfterViewInit(): void {
    this.ss.index('').subscribe((res) => {
      // let  store_item: Array<any> = [];
      for(let i=0;i<res.length;i++){
        let dt = {};
        dt['store_name'] = res[i]['name'];
        dt['store_id'] = res[i]['id'];
        this.store_item.push(dt);
      }
      // this.myGrid.setcolumnproperty('from_store_name', 'filteritems', this.store_item);
    }, (error) => {
      console.info(error);
    }); 
  }

  createQuickAccess() {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(QuickaccessComponent);
    this.quickAccess.clear();
    const dyynamicComponent = this.quickAccess.createComponent(componentFactory);
  }

  getList() {
    let myUrl = this.apiUrl + 'requisition_detail';
    this.source =
      {
        datatype: 'json',
        datafields: [
          { name: 'id', type: 'string' },
          { name: 'indent_no', type: 'string' },
          { name: 'indent_date', type: 'date' },
          { name: 'from_store_id', type: 'string' },
          { name: 'to_store_id', type: 'string' },
          { name: 'from_store_name', type: 'string', map: 'from_store>name' },
          { name: 'to_store_name', type: 'string', map: 'to_store>name' }
        ],
        id: 'id',
        root: 'data',
        pagesize: 20,
        url: myUrl,
        totalrecords: 100,
        data: this.userData,
        filter: () => {
          // update the grid and send a request to the server.
          this.myGrid.updatebounddata('filter');
        },
        sort: () => {
          // update the grid and send a request to the server.
          this.myGrid.updatebounddata('sort');
        },
        beforeprocessing: (data) => {

        }
      }
    this.dataAdapter = new jqx.dataAdapter(this.source, {
      formatData: (data) => {
        if (this.report) {
          data['limit'] = 'all';
          data['report'] = true;
          data['report_type'] = 'excel';
        } else {
          data['page'] = data['pagenum'] + 1;
          data['limit'] = data['pagesize'];
        }
        return data;
      },
      beforeSend: (jqXHR, settings) => {
        if (this.report) {
          this.request_url = settings.url;
        }
      }, downloadComplete: (data, status, xhr) => {
        this.source.totalrecords = data.data.total;
        let tData = data.data.data;
        return tData;
      }, loadError: (jqXHR, status, error) => {
        if (this.report) {
          this.report = false;
          let anchor = document.createElement('a');
          anchor.style.cssText = 'display: none';
          document.body.appendChild(anchor); // Attach to document
          anchor.href = this.request_url;
          anchor.click();
          this.myGrid.updatebounddata();
        }else{
          let messageDiv: any = document.getElementById('listingMessage');
          messageDiv.innerText = error;
          this.winNotification.open();
          this.jqxLoader.close();
        }
      }
    });

    this.columns =
      [
        {
          text: 'S.N', sortable: false, filterable: false, editable: false,
          groupable: false, draggable: false, resizable: false,
          datafield: 'id', columntype: 'number', width: 50,
          cellsrenderer: function (row, column, value) {
            return "<div style='margin:4px;'>" + (value + 1) + "</div>";
          }
        },
        { text: 'Indent Date', datafield: 'indent_date', cellsformat: 'yyyy-MM-dd', columntype: 'date', filtertype: 'range' },
        { text: 'Indent Number', datafield: 'indent_no', columntype: 'textbox', filtercondition: 'starts_with' },
        { text: 'From Store', datafield: 'from_store_id', displayfield: 'from_store_name', columntype: 'combobox', filtertype: 'list' },
        { text: 'To store', datafield: 'to_store_id', displayfield: 'to_store_name', columntype: 'combobox', filtertype: 'list', width: 200 }, // filteritems:this.store_item
        {
          text: 'Req Item Details', datafield: 'view_item_details', columntype: 'button', sortable: false, filterable: false, width: 150,
          cellsrenderer: (): string => {
            return 'View Item Details';
          },
          buttonclick: (row: number): void => {
            this.editrow = row;
            let dataRecord = this.myGrid.getrowdata(this.editrow);
            this.myWindow.draggable(true);
            this.myWindow.title('&nbsp;');
            const componentFactory = this.componentFactoryResolver.resolveComponentFactory(RequisitionItemDetailsViewComponent);
            this.Insert.clear();
            const dynamic_Component = this.Insert.createComponent(componentFactory);
            this.myWindow.open();
            (<RequisitionItemDetailsViewComponent>dynamic_Component.instance).RequisitionDetailViewData = dataRecord;

          }
        }, 
        // {
        //   text: 'Actions', datafield: 'Edit', columntype: 'button', sortable: false, filterable: false, width: 70,
        //   cellsrenderer: (): string => {
        //     return 'Edit';
        //   },
        //   buttonclick: (row: number): void => {
        //     this.editrow = row;
        //     let dataRecord = this.myGrid.getrowdata(this.editrow);
        //     this.myWindow.draggable(true);
        //     this.myWindow.title('Edit');
        //     const componentFactory = this.componentFactoryResolver.resolveComponentFactory(RequisitionDetailMasterEditComponent);
        //     this.Insert.clear();
        //     const dynamic_Component = this.Insert.createComponent(componentFactory);
        //     this.myWindow.open();
        //     (<RequisitionDetailMasterEditComponent>dynamic_Component.instance).RequisitionDetailEditData = dataRecord;
        //     (<RequisitionDetailMasterEditComponent>dynamic_Component.instance).UpdatedData.subscribe(result => {
        //       if (result.result == true) {
        //         let messageDiv = document.getElementById('message');
        //         messageDiv.innerText = result['message'];
        //         this.msgNotification.open();
        //         this.myWindow.hide();
        //         this.myGrid.updatebounddata('cells');
        //         dynamic_Component.destroy();

        //       } else {
        //         let messageDiv = document.getElementById('message');
        //         messageDiv.innerText = result['message'];
        //         this.msgNotification.open();
        //       }
        //     });

        //     (<RequisitionDetailMasterEditComponent>dynamic_Component.instance).cancelData.subscribe(result => {
        //       if (result == true) {
        //         this.myWindow.hide();
        //         dynamic_Component.destroy();
        //       }
        //     });
        //   }
        // }
      ];
  }

  rendergridrows = (params: any): any[] => {
    return params.data;
  }

  rendertoolbar = (toolbar: any): void => {
    let container = document.createElement('div');
    container.style.margin = '5px';

    let ReqDetailMasterAddRow = document.createElement('div');
    let ReqDetailMasterDeleteRow = document.createElement('div');
    let buttonContainer6 = document.createElement('div');
    let buttonContainer5 = document.createElement('div');

    ReqDetailMasterAddRow.id = 'ReqDetailMasterAddRow';
    ReqDetailMasterDeleteRow.id = 'ReqDetailMasterDeleteRow';
    buttonContainer5.id = 'buttonContainer5';
    buttonContainer6.id = 'buttonContainer6';

    ReqDetailMasterAddRow.style.cssText = 'float: left';
    ReqDetailMasterDeleteRow.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer5.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer6.style.cssText = 'float: left; margin-left: 5px';

    container.appendChild(ReqDetailMasterAddRow);
    container.appendChild(ReqDetailMasterDeleteRow);
    container.appendChild(buttonContainer5);
    container.appendChild(buttonContainer6);
    toolbar[0].appendChild(container);

    let addRowButton = jqwidgets.createInstance('#ReqDetailMasterAddRow', 'jqxButton', { width: 105, value: 'Add New Row', theme: 'energyblue' });
    let deleteRowButton = jqwidgets.createInstance('#ReqDetailMasterDeleteRow', 'jqxButton', { width: 150, value: 'Delete Selected Row', theme: 'energyblue' });
    let resetSearchdata = jqwidgets.createInstance('#buttonContainer5', 'jqxButton', { width: 150, value: 'Reset Search Data', theme: 'energyblue' });
    let generateReport = jqwidgets.createInstance('#buttonContainer6', 'jqxButton', { width: 150, value: 'Generate Report', theme: 'energyblue' });
    addRowButton.addEventHandler('click', () => {
      this.myWindow.draggable(true);
      this.myWindow.title('Add Requisition Details');
      this.myWindow.open();
      const componentFactory = this.componentFactoryResolver.resolveComponentFactory(RequisitionDetailMasterAddComponent);
      this.Insert.clear();
      const dynamic_Component = this.Insert.createComponent(componentFactory);
      (<RequisitionDetailMasterAddComponent>dynamic_Component.instance).AddedData.subscribe(result => {
        if (result.result == true) {
          let messageDiv = document.getElementById('message');
          messageDiv.innerText = result['message'];
          this.msgNotification.open();
          this.myWindow.hide();
          this.myGrid.updatebounddata('cells');
          dynamic_Component.destroy();

        } else {
          let messageDiv = document.getElementById('message');
          messageDiv.innerText = result['message'];
          this.msgNotification.open();
        }
      });

      (<RequisitionDetailMasterAddComponent>dynamic_Component.instance).cancelData.subscribe(result => {
        if (result == true) {
          this.myWindow.hide();
          dynamic_Component.destroy();
        }
      });
    });
    resetSearchdata.addEventHandler('click', () => {
      this.myGrid.clearfilters();
    });
    generateReport.addEventHandler('click', () => {
      this.report = true;
      this.myGrid.updatebounddata();
    });
    deleteRowButton.addEventHandler('click', () => {
      let id = this.myGrid.getselectedrowindexes();
      let ids = [];
      let rowscount = this.myGrid.getdatainformation().rowscount;
      for (let i = 0; i < id.length; i++) {
        let dataRecord = this.myGrid.getrowdata(Number(id[i]));
        ids.push(dataRecord.id);
      }
      if (ids.length > 0 && ids.length <= parseFloat(rowscount)) {
        if (confirm("Are you sure? You Want to delete")) {
          // let id = this.myGrid.getrowid(selectedrowindex);
          this.jqxLoader.open();
          this.rdm.destroy('[' + ids + ']').subscribe(data => {
            let messageDiv = document.getElementById('message');
            messageDiv.innerText = data['message'];
            this.msgNotification.open();
            this.jqxLoader.close();
            this.myGrid.updatebounddata('cells');
          })
        }
      } else {
        let messageDiv = document.getElementById('message');
        messageDiv.innerText = 'Please select some item to delete';
        this.msgNotification.open();
      }
    })
  };


}



