import { Component, OnInit, Input, Inject, AfterViewInit, ChangeDetectorRef, Output, EventEmitter, ViewChild } from '@angular/core';
import { RequisitionDetailMasterService } from '../../../../shared';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';

@Component({
  selector: 'app-requisition-item-details-view',
  templateUrl: './requisition-item-details-view.component.html',
  styleUrls: ['./requisition-item-details-view.component.scss']
})
export class RequisitionItemDetailsViewComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myGrid') myGrid: jqxGridComponent;
  @Input('RequisitionDetailViewData') RequisitionDetailViewData;

  source: any;
  dataAdapter: any;
  columns: any[];
  apiUrl: string;
  requisition_item: Array<any> = [];
  contractorsource: any;
  contractorAdapter: any;

  constructor( @Inject('API_URL') apiUrl: string,
    private cdr: ChangeDetectorRef,
    private rdms: RequisitionDetailMasterService) {
    this.apiUrl = apiUrl;
  }

  ngOnInit() {
    this.getItem();
  }

  ngAfterViewInit() {
    this.jqxLoader.open();
    this.rdms.show(this.RequisitionDetailViewData.id).subscribe(
      result => {
        this.requisition_item = result['data'].requisition_item_details;
        for (var _i = 0; _i < this.requisition_item.length; _i++) {
          this.requisition_item[_i]['name'] = this.requisition_item[_i]['item'] && this.requisition_item[_i]['item'].name;
          this.requisition_item[_i]['unit_name'] = this.requisition_item[_i]['item'] && this.requisition_item[_i]['item']['unit'] && this.requisition_item[_i]['item']['unit'].name;
        }
        this.source.localdata = this.requisition_item;
        this.myGrid.updatebounddata("cells");
        this.jqxLoader.close();
      },
      error => {
        console.log(error);
      }
    );
    this.cdr.detectChanges();
  }

  getItem() {
    this.source =
      {
        localdata: {},
        datatype: 'json',
        datafields:

          [
            { name: 'name', type: 'string' },
            { name: 'item_id', type: 'string' },
            { name: 'quantity', type: 'number' },
            { name: 'unit_id', type: 'number' },
            { name: 'unit_name', type: 'string' },
            { name: 'remarks', type: 'string' },
          ],
      };

    this.dataAdapter = new jqx.dataAdapter(this.source);
    this.contractorsource =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [

          { name: 'item_id', type: 'string' },
          { name: 'name', type: 'string' },
        ],
        url: this.apiUrl + 'item?limit=all'
      };
    this.contractorAdapter = new jqx.dataAdapter(this.contractorsource, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        for (var _i = 0; _i < tData.length; _i++) {
          tData[_i]['item_id'] = tData[_i]['id'];
        }
        return tData;
      }
    });
    this.columns =
      [
        {
          text: 'Item Name', datafield: 'item_id', displayfield: 'name', columntype: 'combobox',
          initeditor: (row: number, cellvalue: string, editor: any, celltext: string, cellwidth: number, cellheight: number): void => {
            editor.jqxComboBox({
              source: this.contractorAdapter,
              displayMember: "name",
              valueMember: "item_id"
            });
          }
        },
        { text: 'Quantity', columntype: 'textbox', datafield: 'quantity' },
        { text: 'Unit', datafield: 'unit_id', displayfield:'unit_name', columntype: 'textbox' },
        { text: 'Remarks', datafield: 'remarks', columntype: 'textbox' },
      ];

  }
  rendertoolbar = (toolbar: any): void => {
    let container = document.createElement('div');
    container.style.margin = '5px';
    let headingContainer4 = document.createElement('div');
    let buttonContainer5 = document.createElement('div');

    headingContainer4.id = 'headingContainer4';
    buttonContainer5.id = 'buttonContainer5';

    headingContainer4.style.cssText = 'float: left;padding:5px;margin-right:10px;font-weight:bold;';
    buttonContainer5.style.cssText = 'float: right';

    headingContainer4.innerHTML = 'Requisition Item Details:';

    container.appendChild(headingContainer4);
    container.appendChild(buttonContainer5);
    toolbar[0].appendChild(container);

};

}



