import { Component, OnInit, Output, Inject, EventEmitter, ViewChild, ElementRef, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { RequisitionDetailMasterService, CodeCheckerService, CurrentUserService,ErrorHandleService, StoreService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxWindowComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxwindow';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { ItemMasterService } from '../../../../shared';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxDateTimeInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxdatetimeinput';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-requisition-detail-master-add',
  templateUrl: './requisition-detail-master-add.component.html',
  styleUrls: ['./requisition-detail-master-add.component.scss']
})
export class RequisitionDetailMasterAddComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('reqdetailAddGrid') reqdetailAddGrid: jqxGridComponent;
  @ViewChild('fComboBox') fComboBox: jqxComboBoxComponent;
  @ViewChild('tComboBox') tComboBox: jqxComboBoxComponent;
  @ViewChild('datetime') datetime: jqxDateTimeInputComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @Output() AddedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();


  apiUrl: string;
  requisitionDetailForm: FormGroup;
  requisition_item: Array<any> = [];
  cellData: any;
  store_data: any;
  storeAdapter: any;
  estate_data: any;
  estateAdapter: any;

  storeData:any;
  toStoreData:any;

  formStore: boolean = false;
  toStore: boolean = false;

  userData: any = {};

  constructor(
    private reqDetailService: RequisitionDetailMasterService,
    private itemService: ItemMasterService,
    private fb: FormBuilder,
    @Inject('API_URL') apiUrl: string,
    private cdr: ChangeDetectorRef,
    private ehs: ErrorHandleService,
    private ccs: CodeCheckerService,
    private cus: CurrentUserService,
    private sms: StoreService
  ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.requisitionDetailForm = fb.group({
      'indent_no': [null, Validators.required],
      'indent_date': [null, Validators.required],
    });
  }

  ngOnInit() {
    // this.store_data =
    //   {
    //     datatype: "json",
    //     contentType: "application/json; charset=utf-8",
    //     datafields: [
    //       { name: 'id' },
    //       { name: 'name' },
    //     ],
    //     data: this.userData,
    //     url: this.apiUrl + 'store?limit=all',
    //   };
    // this.storeAdapter = new jqx.dataAdapter(this.store_data, {
    //   downloadComplete: (data, status, xhr) => {
    //     let tData = data.data;
    //     return tData;
    //   }
    // });

    this.sms.index({limit:'all'}).subscribe(
      response => {
        this.storeData = response;
        this.toStoreData = response.filter(x => x.type == 'HO');
      }, error => {
        console.log(error)
      }
    )

  }

  ngAfterViewInit(){
    let codeData = {
      find_is_no:'requisition',
      return_type: 'for_client'
    }
    this.ccs.get(codeData).subscribe(
      response => {
        this.requisitionDetailForm.controls['indent_no'].setValue(response['indent_no']);
      },
      error => {
        console.log(error);
      }
    );
    this.datetime.value(new Date());
    this.cdr.detectChanges();
  }

  source: any =
    {
      localdata: this.requisition_item,
      datatype: 'json',
      datafields:
        [
          { name: 'item_id', type: 'string', map: 'name' },
          { name: 'quantity', type: 'number' },
          { name: 'unit_id', type: 'number' },
          { name: 'unit_name', type: 'string' },
          { name: 'remarks', type: 'string' },
        ]
    };

  dataAdapter: any = new jqx.dataAdapter(this.source);
  columns: any[] =
    [
      {
        text: 'Item Name', datafield: 'item_id', displayfield: 'name', columntype: 'combobox', width: 140,
        createeditor: (row: number, column: any, editor: any): void => {
          let contractorsource =
            {
              datatype: "json",
              contentType: "application/json; charset=utf-8",
              datafields: [
                { name: 'id' },
                { name: 'name' },
              ],
              data: this.userData,
              async: false,
              url: this.apiUrl + 'item?limit=all&transact=1'
            };
          let contractorAdapter = new jqx.dataAdapter(contractorsource, {
            downloadComplete: (data, status, xhr) => {
              let tData = data.data;
              return tData;
            }
          });
          editor.jqxComboBox({
            source: contractorAdapter,
            displayMember: "name",
            valueMember: "id"
          });
        },
        cellvaluechanging: (row: number, datafield: string, columntype: any, oldvalue: any, newvalue: any): void => {
          // if (newvalue != oldvalue) {
            let currentRow = this.reqdetailAddGrid.getrowdata(row).boundindex;
            this.itemService.show(newvalue['value']).subscribe(
              result => {
                  if(result['data']['quantity']){
                    this.reqdetailAddGrid.setcellvalue(currentRow,'quantity',result['data'].quantity);
                  }else{
                    this.reqdetailAddGrid.setcellvalue(currentRow,'quantity',0);
                  }
                if(result['data']['unit']){
                  this.reqdetailAddGrid.setcellvalue(currentRow,'unit_name',result['data']['unit'].name);
                  this.reqdetailAddGrid.setcellvalue(currentRow,'unit_id',result['data'].unit_id);
                }

              },
              error => {
                console.log(error);
              }
            )
          // }
        },
        // cellsrenderer: (index: number, datafield: string, value: any, defaultvalue: any, column: any, rowdata: any): void => {
        //   if(rowdata.item_id){
        //     this.itemService.show(rowdata.item_id).subscribe(
        //       result => {
        //         if(result['data']['unit']){
        //           this.reqdetailAddGrid.setcellvaluebyid(String(index),'unit_name',result['data']['unit'].name);
        //           this.reqdetailAddGrid.setcellvaluebyid(String(index),'unit_id',result['data'].unit_id);
        //         }
        //         if(result['data']['quantity']){
        //           this.reqdetailAddGrid.setcellvaluebyid(String(index),'quantity',result['data'].quantity);
        //         }  
        //       },
        //       error => {
        //         console.log(error);
        //       }
        //     )
        //   }
        // }
      },
      { text: 'Quantity', columntype: 'textbox', datafield: 'quantity', width: 120 },
      { text: 'Unit', datafield: 'unit_id', editable: false ,displayfield:'unit_name', columntype: 'textbox', width: 70 },
      { text: 'Remarks', datafield: 'remarks', columntype: 'textbox'},
      {
        text: 'Actions', datafield: 'Delete', columntype: 'button', width: 70,
        cellsrenderer: (): string => {
          return 'Delete';
        },
        buttonclick: (row: number): void => {
          let rowid = this.reqdetailAddGrid.getrowid(row)
          this.reqdetailAddGrid.deleterow(rowid);
        }
      }
    ];

  // rendertoolbar = (toolbar: any): void => {
  //   console.log('Add toolbar rendered');
  //   console.log(toolbar);
  //   let container = document.createElement('div');
  //   container.style.margin = '5px';

  //   let ReqDetailMasterAddAddRow = document.createElement('div');

  //   ReqDetailMasterAddAddRow.id = 'ReqDetailMasterAddAddRow';

  //   ReqDetailMasterAddAddRow.style.cssText = 'float: left';

  //   container.appendChild(ReqDetailMasterAddAddRow);
  //   toolbar[0].appendChild(container);

  //   let addRowButton = jqwidgets.createInstance('#ReqDetailMasterAddAddRow', 'jqxButton', { width: 155, value: 'Add New Item' });


  //   addRowButton.addEventHandler('click', () => {
  //     let datarow = this.generaterow();
  //     this.reqdetailAddGrid.addrow(null, datarow);
  //   })
  // };

  addNewItem(){
    let datarow = this.generaterow();
    this.reqdetailAddGrid.addrow(null, datarow);
  }

  generaterow(): any {
    let row = {};
    row['item_id'] = '';
    row['quantity'] = ''
    row['unit_id'] = '';
    row['remarks'] = '';
    return row;
  }

  // currentItem:any;
  // getItemByItemId(id){
  //   if(id){

  //   }
  // }


  saveBtn(post) {
    post['requisitionItemDetails'] = this.reqdetailAddGrid.getrows();
    post['from_store_id'] = this.fComboBox.val();
    post['to_store_id'] = this.tComboBox.val();
    this.myValidator.validate(document.getElementById('requisitionDetailForm'));
    if (post['from_store_id'] && post['to_store_id']) {
      this.jqxLoader.open();
      this.reqDetailService.store(post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.AddedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        }
      );
    }
  }
  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
    [
      { input: '.indent_noInput', message: 'Indent no is required!', action: 'keydown, blur', rule: 'required' },
      {
        input: '.indent_dateInput', message: ' Indent date is required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
          let data = this.datetime.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.from_store_id', message: 'From store is required!', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.fComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.to_store_id', message: 'To store is required!', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.tComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      }

    ];

    fromStoreChanged() {
      this.formStore = this.fComboBox.val()?true:false;
    }

    toStoreChanged() {
      this.toStore = this.tComboBox.val()?true:false;
    }

}



