import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { RequisitionDetailMasterRoutingModule } from './requisition-detail-master-routing.module';
import { RequisitionDetailMasterComponent } from './requisition-detail-master.component';
import { RequisitionDetailMasterAddComponent } from './requisition-detail-master-add/requisition-detail-master-add.component';
import { RequisitionDetailMasterEditComponent } from './requisition-detail-master-edit/requisition-detail-master-edit.component';
import { RequisitionItemDetailsViewComponent } from './requisition-item-details-view/requisition-item-details-view.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    RequisitionDetailMasterRoutingModule,
  ],
  entryComponents:[
    RequisitionDetailMasterAddComponent,
    RequisitionDetailMasterEditComponent,
    RequisitionItemDetailsViewComponent
  ],
  declarations: [
    RequisitionDetailMasterComponent,
    RequisitionDetailMasterAddComponent,
    RequisitionDetailMasterEditComponent,
    RequisitionItemDetailsViewComponent
  ]
})
export class RequisitionDetailMasterModule { }



